import React, { Fragment } from 'react';
import {
  H1,
  H2,
  H3,
  Content,
  InfoText,
  CodeArea,
  CodeComment,
  CodeFrame,
} from 'components/styles';

const Basics = () => (
  <Fragment>
    <H1>2. <span>Bsico</span></H1>
    <Content>
      <InfoText>
        Os cdigos comeam por aqui! Nesse captulo, mostro conceitos bsicos e fundamentais ao JavaScript. Note que
        os exemplos esto no padro &quot;antigo&quot; <strong>(pr-ES6)</strong>, as diferenas sero explicadas
        depois. Para aqueles que no esto familiarizados, a linguagem passou por&nbsp;
        <strong>evolues tcnicas e sintticas</strong> que flexibilizaram e limitaram problemas que eram causados por
        diferenas semnticas entre JS e outras linguagens de programao.
        <br /><br />
        Aqui, so exemplificadas&nbsp;
        <strong>operaes, atribuies, tipos de dados, estruturas e caractersticas de interpretao</strong>&nbsp;
        que so essenciais para entender o funcionamento e o comportamento dos cdigos. Aproveitei para enfatizar,
        tambm, alguns pontos que so considerados <strong>legado ou antipadro</strong>, e como eles so contornados
        pelo ES6 em diante.
      </InfoText>
      <H2>2.1. Hello World</H2>
      <CodeArea>
        <CodeComment>
          Reza a lenda que se voc aprender uma linguagem sem fazer um <strong>&quot;Hello World!&quot;</strong> uma
          maldio  passada para sua vida. Felizmente,  to simples quanto chamar o mtodo&nbsp;
          <strong>console.log();</strong>
        </CodeComment>
        <CodeFrame
          title="basics/1_hello.js"
          width="100%"
          height="100%"
          src="//jsfiddle.net/uz2ryax3/1/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
      <H2>2.2. Operadores</H2>
      <CodeArea>
        <CodeComment>
          As operaes <strong>algbricas e booleanas</strong> funcionam como esperado (e com sintaxe muito familiar).
          No exemplo, as operaes so aplicadas em valores <strong>number e boolean</strong>, mas ser mostrado adiante
          que  possvel fazer entre diversos tipos de dados.
          <H3>== vs ===</H3>
          Observe que nas comparao ao lado h o uso de <strong>===</strong>. Este operador pode parecer um pouco
          estranho, mas sua utilidade  muito grande. Alm de comparar os valores, como o operador&nbsp;
          <strong>==</strong> tradicionalmente faz, o <strong>=== (tambm chamado de eqeqeq)</strong>&nbsp;
          compara os tipos de dados dos operandos.
          <br /><br />
          <strong>
            De fato, a utilidade do ===  to grande que ele se tornou padro na linguagem e deve ser utilizado sempre
            que for necessrio comparar dois valores.
          </strong>
        </CodeComment>
        <CodeFrame
          title="basics/1_hello.js"
          width="100%"
          height="100%"
          src="//jsfiddle.net/kdLpyua4/1/embedded/js,result/dark/"
          frameBorder="0"
        />
      </CodeArea>
    </Content>
  </Fragment>
);

export default Basics;



